<template>
<div id="addproduct">
<van-nav-bar
  title="添加产品"
  left-arrow
  class="addproduct-nav"
   @click-left="leftClick"
/>
	<div class="addproduct-content">
		<van-cell-group inset >
			<h4>基础信息</h4>
			<!-- 输入任意文本 -->
			<van-field v-model="productInfo.productName" label="产品名称" placeholder="请输入产品名称" />
			<van-field v-model="productInfo.cid" label="类型" placeholder="请输入产品类型" />
			<van-field v-model="productInfo.encoded" label="编码" placeholder="请输入编码" />
			<van-field v-model="productInfo.color" label="颜色" placeholder="请输入颜色" />
			<van-field v-model="productInfo.specs" label="规格" placeholder="请输入规格" />
			<van-field v-model="productInfo.weight" label="重量" placeholder="请输入重量" />
			<van-field v-model="productInfo.stock" label="库存" placeholder="请输入库存" />
		</van-cell-group>
		<van-cell-group inset >
			<h4>其他信息</h4>
			<!-- 输入任意文本 -->
			<van-field v-model="productInfo.manufacturer" label="生产商" placeholder="请输入生产商" />
			<van-field v-model="productInfo.price" label="最低价" placeholder="请输入最低价" />
			<van-field v-model="productInfo.costs" label="成本价" placeholder="请输入成本价" />
			<van-field v-model="productInfo.newprice" label="最高价" placeholder="请输入最高价" />
		</van-cell-group>
		<van-cell-group inset>
			<h4>备注</h4>
			<van-field
				v-model="productInfo.description"
				rows="2"
				autosize
				type="textarea"
				maxlength="50"
				placeholder="请输入留言"
				show-word-limit
			/>
		</van-cell-group>
	</div>
	<div class="bottom-box">
		<i class="send-btn" @click="sendClick">保存</i>
	</div>
</div>
	
</template>

<script>
import { NavBar,Field, CellGroup} from 'vant'

import {addProduct} from 'network/product'

export default {
  name: 'AddProduct',
	components:{
		[NavBar.name]:NavBar,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
	},
	data(){
		return{
			productInfo:{
				productName:'',				//名称
				cid:'',						//类型
				price:'',					//最低价
				costs:'',					//成本价格
				newprice:'',				//最高价
				encoded:'',					//编码
				manufacturer:'',			//生产商
				color:'',					//颜色
				specs:'',					//规格
				weight:'',					//重量
				stock:0,					//库存
				description:'',				//留言
				content:"默认内容",			//内容
				imgurl:"asc.jpg",			//图片地址
			}
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},
		//保存按钮
		sendClick(){
			//保存前需要各种验证 这里可以写进一个工具类里面
			let info=this.productInfo							//简便一下-省点字数
			let _self=this
			//各种验证
			if(this.$checkIsNull(info.productName)){
				this.$toast('产品名称不能为空')
				return
			}
			if(this.$checkIsNull(info.specs)){
				this.$toast('产品规格不能为空')
				return
			}
			if(this.$checkIsNull(info.weight)){
				this.$toast('产品重量不能为空')
				return
			}
			if(this.$checkIsNull(info.manufacturer)){
				this.$toast('生产商不能为空')
				return
			}
			if(this.$checkIsNull(info.costs)){
				this.$toast('成本价不能为空')
				return
			}
			if(this.$checkIsNull(info.price)){
				this.$toast('销售价不能为空')
				return
			}
			
			addProduct(this.productInfo).then(res=>{
				_self.$toast(res)
			})
			//如果成功就清除所有表单按钮并且跳转
			this.$router.go(-1)
		}
	}
}
</script>

