import {request} from './request'

export function getProduct(keywords,page=3,time=0) {
  return request({
	url:'/vuedata/product.php',
    params:{
		keywords,
		page,
		time,
    }
  });
}

export function getProductDetail(id) {
  return request({
	url:'/vuedata/productdetail.php',
    params:{
		id,
    }
  });
}

